<template>
  <uploader ref="uploader" @file-added="fileAdded" :options="optionsPlugin" :autoStart="autoStart" class="upload">
    <uploader-unsupport></uploader-unsupport>
    <uploader-drop class="upload-drop" v-if="isDrop && canUpload">
      <p><i class="fa fa-cloud-upload"></i></p>
      <p></p>
    </uploader-drop>
    <div class="upload-btns" v-if="canUpload">
        <uploader-btn type="button" class="add-file" :attrs="attrs"></uploader-btn>
        <button type="button" v-if="!autoStart" class="uploader-btn start-upload" @click="upload"></button>
    </div>
    <div class="tips" v-if="isIE10Plus">IE.</div>
    <template>
      <div class="existed-file-list file" v-for="file in existedFiles" :key="file.id">
        <slot :file="file" name="existed-file">
          <div class="file-name"><i class="fa" :class="file.oldName | fileExtension | fileIcon"></i>{{file.oldName}}</div>
          <div class="file-size">{{file.docSize}}</div>
          <div class="file-finished">
            
          </div>
          <div class="file-actions">
            <a  href="javascript:void(0);" v-if="file.type === 'image'" title="" @click="previewImg(file)">
              <i class="fa fa-eye"></i>
            </a>
            <a :href="docURL+'/doc/download/'+file.id">
                <i class="fa fa-download"></i>
            </a>
            <a href="javascript:void(0);" v-if="canUpload" @click="del(file,true)">
              <i class="fa fa-times"></i>
            </a>
            <slot name="file-actions-slot" :file="file"></slot>
          </div>
          <el-dialog class="preview" :visible.sync="file.dialogVisible" @close="dialogClose">
            <img :src="docURL + file.docUrl" class="preview-image">
          </el-dialog>
        </slot>
      </div>
    </template>
    <uploader-list>
        <template slot-scope="fileList"> 
          <uploader-file class="file" v-for="file in fileList.fileList" :key="file.id" :list="true" :file="file">
              <template slot-scope="uploadFile">
                <slot :file="uploadFile" name="upload-file">
                    <div class="hide">{{uploadFile}}</div>
                    <div class="file-name"><i class="fa" :class="uploadFile.extension | fileIcon"></i>{{uploadFile.file.name}}</div>
                    <div class="file-size">{{uploadFile.file.size/1024>1024?(uploadFile.file.size/1024/1024).toFixed(2)+"MB":(uploadFile.file.size/1024).toFixed(2)+"KB"}}</div>
                    <div class="file-status">{{uploadFile.status | statusFilter}}</div>
                    <div class="file-progress">
                      <div class="file-progress-wrap">
                        <div class="file-progress-current" 
                            :style="{width:(uploadFile.file._prevProgress).toFixed(3)*100+'%'}"
                            :class="{'success':uploadFile.file._prevProgress === 1 &&  !uploadFile.file.error,
                                      'error':uploadFile.file.error}"
                        >
                        </div>
                        <span class="file-progress-number">{{(uploadFile.file._prevProgress).toFixed(3)*100+'%'}}</span>
                      </div>
                    </div>
                    <div class="file-actions">
                      <a href="javascript:void(0);" title="" @click="previewImg(uploadFile.file)" v-if="uploadFile.fileCategory === 'image' && uploadFile.status ==='success'">
                        <i class="fa fa-eye"></i>
                      </a>
                      <a href="javascript:void(0);" @click="pause(uploadFile)" v-if="uploadFile.status!=='success'">
                        <i class="fa fa-pause"></i>
                      </a>
                      <a href="javascript:void(0);" @click="del(uploadFile)">
                        <i class="fa fa-times"></i>
                      </a>
                      <slot name="file-actions-slot" :file="uploadFile.file"></slot>
                    </div>
                    <el-dialog class="preview" :visible.sync="uploadFile.file.dialogVisible" @close="dialogClose">
                      <img :src="docURL + uploadFile.file._fileUrl" class="preview-image">
                    </el-dialog>
                    <div class="hide">{{uploadFile}}</div>
                </slot>
              </template>
          </uploader-file>
        </template>
    </uploader-list>
  </uploader>
</template>

<script>
import axios from "axios";
import qs from "qs";
import {
  iconFilterMixin,
  apiDataMixin,
  typeFilterMixin
} from "../common/mixins";
import { getExtension, extension2Type } from "../common/utils";
import url from "url";
export default {
  mixins: [iconFilterMixin, apiDataMixin, typeFilterMixin],
  created() {
    const vm = this;
    //
    if (this.types.length > 0) {
      const types = Array.isArray(this.types) ? this.types : [this.types];
      types.forEach(ele => {
        if (ele.indexOf("/") > -1) {
          if (ele.indexOf("*") > -1) {
            vm.involveTypes.push(ele);
          } else {
            vm.specificTypes.push(ele);
          }
        }
        if (ele.indexOf(".") > -1) {
          vm.expectExtensions.push(ele);
        }
      });
    }
    //
    if (typeof this.fileIds === "undefined" || this.fileIds.length < 0) {
      return false;
    }
    this.loadFiles(this.fileIdsString);
  },
  mounted() {
    const uploader = this.uploader;
    const vm = this;
    uploader.on("fileSuccess", function(rootFile, file, message, chunk) {
      if (JSON.parse(message).now_id) {
        vm.$set(file, "_fileId", JSON.parse(message).now_id);
        vm.$set(file, "_fileUrl", JSON.parse(message).now_url);
        vm.uploadedIds.push(JSON.parse(message).now_id);
        vm.uploadedFiles.push(file);
        vm.waitingNum--;
      } else {
        if (JSON.parse(message).info) {
          vm.$message({
            type: "error",
            message: `${
              JSON.parse(message).info
            }`
          });
          console.error(file);
        }
      }
    });
  },
  props: {
    //
    docURL: {
      type: String,
      default: "http://d.test.app.pubob.ob.io/"
    },
    //simple-uploader.js
    options: {
      type: Object,
      default: () => {}
    },
    //
    isDrop: {
      type: Boolean,
      default: true
    },
    //
    canUpload: {
      type: Boolean,
      default: true
    },
    //id
    fileIds: {
      type: [String, Array],
      default: () => []
    },
    //
    types: {
      type: [Array],
      default: () => []
    },
    //
    limitNum: {
      type: Number
    },
    //KB
    limitSize: {
      type: Number
    },
    //
    willDelByView: {
      type: Boolean,
      default: false
    },
    //
    autoStart: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      uploadedIds: [], //id
      uploadedFiles: [], //
      existedFiles: [], //
      attrs: {
        accept: this.types
      },
      involveTypes: [], //*
      specificTypes: [], //*
      expectExtensions: [], //
      //
      addedNum: (() => {
        return Array.isArray(this.fileIds)
          ? this.fileIds.length
          : this.fileIds.split(",").length;
      })(),
      waitingNum: 0,
      isIE10Plus: (() => window.navigator.msPointerEnabled)(),
      delByViewIdsCache: [] //id
    };
  },
  filters: {
    statusFilter(status) {
      let fileStatusText = {
        success: "",
        error: "",
        uploading: "",
        paused: "",
        waiting: ""
      };
      return fileStatusText[status] || "";
    }
  },
  computed: {
    uploader() {
      return this.$refs.uploader.uploader;
    },
    expectTypes() {
      return Array.isArray(this.types) ? this.types : [this.types];
    },
    fileIdsString() {
      return Array.isArray(this.fileIds)
        ? this.fileIds.join(",")
        : this.fileIds;
    },
    APIMap() {
      const map = new Map();
      const keys = Object.keys(this.API);
      keys.forEach(key => {
        map.set(key, url.resolve(this.docURL, this.API[key]));
      });
      return map;
    },
    optionsPlugin() {
      return Object.assign({}, this.options, {
        target: this.APIMap.get("upload"),
        chunkSize: 100 * 1024 * 1024,
        testChunks: false
      });
    }
  },
  methods: {
    upload() {
      this.uploader.upload();
    },
    /**
     * @description id
     * @param ids {object} 
     */
    postDeleteRequest(ids) {
      return new Promise((resolve, reject) => {
        axios
          .post(
            this.APIMap.get("delete"),
            qs.stringify({
              ids: Array.isArray(ids) ? ids.join(",") : ids
            })
          )
          .then(res => {
            let idsArr = Array.isArray(ids) ? ids : ids.split(",");
            let resIdsArr = res.data.split(",");
            let willResolve = idsArr.length === resIdsArr.length;
            // let includeIdArr = [];
            if (!willResolve) {
              reject(res.data);
              return;
            }
            willResolve = resIdsArr.every(id => resIdsArr.indexOf(id) > -1);
            if (willResolve) {
              resolve(res.data);
            } else {
              reject(res.data);
            }
          })
          .catch(err => {
            // this.$message({
            //   type: "info",
            //   message: ""
            // });
            console.error(err);
          });
      });
    },
    //TODO: deleteFilesNotInView
    /**
     * @description 
     */
    deleteFilesNotInView() {
      return this.postDeleteRequest(this.delByViewIdsCache);
    },
    /**
     * @description 
     * @param file {object} 
     * @param idKey {string} 
     */
    delFileAsync(file, idKey = "_fileId") {
      return new Promise((resolve, reject) => {
        this.postDeleteRequest(file[idKey])
          .then(data => {
            if (data === file[idKey]) {
              resolve(file);
            } else {
              this.$message({
                type: "info",
                message: ""
              });
              reject(data);
            }
          })
          .catch(err => {
            this.$message({
              type: "info",
              message: ""
            });
            reject(err);
            console.error(err);
          });
      });
    },
    /**
     * @description uploadedIdsuploadedFilesid
     * @param fileId {string} id
     */
    delFromUploadedList(fileId) {
      function delEleByFileIdFromArr(arr, id) {
        arr.forEach((ele, index) => {
          if (ele._fileId === id) {
            arr.splice(index, 1);
          }
        });
      }
      const vm = this;
      let uploaderFileList = vm.uploader.fileList;
      let uploaderFiles = vm.uploader.files;
      delEleByFileIdFromArr(uploaderFileList, fileId);
      delEleByFileIdFromArr(uploaderFiles, fileId);
      delEleByFileIdFromArr(vm.uploadedFiles, fileId);
      vm.uploadedIds.splice(vm.uploadedIds.indexOf(fileId), 1);
    },
    /**
     * @description simple-uploaer
     * @param fileSlot {object} 
     * @param isExistedFile {boolean} 
     */
    del(fileSlot, isExistedFile = false) {
      const vm = this;
      const uploader = this.uploader;
      const file = isExistedFile ? fileSlot : fileSlot.file;
      function delByView(file) {
        vm.addedNum--;
        vm.delFromUploadedList(file._fileId);
        if (isExistedFile) {
          vm.existedFiles.forEach((element, index) => {
            if (element.id === file._fileId) {
              vm.existedFiles.splice(index, 1);
            }
          });
        } else {
          uploader.removeFile(file);
        }
        vm.$message({ message: "", type: "success" });
      }
      this.$emit("click-delete-btn");
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          if (
            isExistedFile ||
            (typeof file.isComplete === "function" && file.isComplete())
          ) {
            if (this.willDelByView) {
              delByView(file);
              this.delByViewIdsCache.push(file._fileId);
              this.$emit("after-delete-confirm", true, file._fileId);
            } else {
              vm.delFileAsync(file).then(file => {
                delByView(file);
                this.$emit("after-delete-confirm", true, file._fileId);
              });
            }
          } else {
            vm.waitingNum--;
            delByView(file);
            this.$emit("after-delete-confirm", true, file._fileId);
          }
        })
        .catch(() => this.$emit("after-delete-confirm", false, file._fileId));
    },
    pause(fileSlot) {
      fileSlot.file.pause();
    },
    /**
     * @description simple-upload
     * @param file {object} 
     */
    previewImg(file) {
      this.$emit("dialog-open");
      this.$emit("dialog-toggle", true);
      file.dialogVisible = true;
    },
    /**
     * @description 
     */
    dialogClose() {
      this.$emit("dialog-close");
      this.$emit("dialog-toggle", false);
    },
    /**
     * @description 
     * @param file {object} 
     */
    fileAdded(file) {
      this.$set(file, "dialogVisible", false);
      const vm = this;
      const validateTypeResult = this.validateType(file);
      const validateSizeResult = this.validateSize(file);
      file._name = file.name;
      if (!validateTypeResult) {
        this.$message({
          type: "error",
          message: `${file.name}`
        });
        this.$nextTick(() => {
          this.uploader.removeFile(file);
        });
        return false;
      }
      if (!validateSizeResult) {
        this.$message({
          type: "error",
          message: `${file.name}${vm.limitSize}KB`
        });
        this.$nextTick(() => {
          this.uploader.removeFile(file);
        });
        return false;
      }
      this.addedNum++;
      if (this.limitNum > 0 && this.limitNum < this.addedNum) {
        this.$message({
          type: "error",
          message: `${this.limitNum}`
        });
        this.uploader.files.forEach(_file => {
          if (file.uniqueIdentifier === _file.uniqueIdentifier) {
          }
        });
        this.addedNum--;
        this.$nextTick(() => {
          this.uploader.removeFile(file);
        });
        return false;
      }
      this.waitingNum++;
    },
    /**
     * @description 
     * @param file {object} 
     */
    validateType(file) {
      const fileType = file.fileType;
      const fileName = file.name;
      const extension = fileName.split(".")[fileName.split(".").length - 1];
      let _result = false;
      //mime
      if (this.specificTypes.indexOf(fileType) > -1) {
        return true;
      }
      //*mime
      this.involveTypes.forEach(ele => {
        const _type = ele.match(/([^*]*)\*$/)[1];
        if (fileType.indexOf(_type) > -1) {
          _result = true;
          return true;
        }
      });
      //
      this.expectExtensions.forEach(ele => {
        const _extension = ele.substring(1);
        if (_extension === extension) {
          _result = true;
          return true;
        }
      });
      return this.types.length > 0 ? _result : true;
    },
    /**
     * @description 
     * @param file {object} 
     */
    validateSize(file) {
      if (
        typeof this.limitSize !== "undefined" &&
        !Number.isNaN(this.limitSize)
      ) {
        return file.size / 1024 < this.limitSize;
      } else {
        return true;
      }
    },
    /**@description 
     * @param ids {string | array} ids
     * @param willReplace {boolean} ,false
     */
    loadFiles(ids, willReplace = false) {
      const vm = this;
      if (ids === "") {
        this.existedFiles = [];
        return false;
      }
      if (this.docURL.length === 0) {
        return false;
      }
      axios
        .post(
          this.APIMap.get("query"),
          qs.stringify({
            file_ids: Array.isArray(ids) ? ids.join(",") : ids
          })
        )
        .then(res => {
          const data = res.data;
          if (data.file_info) {
            if (!Array.isArray(data.file_info)) {
              console.error(data.file_info);
              vm.$message({
                type: "error",
                message: data.info
              });
              return false;
            }
            //TODO:
            //validate type and validate number and validate size
            if (data.file_info.length + vm.addedNum > vm.limitNum) {
              // vm.$message({
              //   type: "error",
              //   message: `${vm.limitNum}${data
              //     .file_info.length + vm.addedNum}`
              // });
              // vm.$emit("errorFn", {
              //   error: "exceeded the limit",
              //   arguments: {
              //     limitNum: vm.limitNum,
              //     uploadedNum: vm.addedNum,
              //     existedNum: data.file_info.length
              //   }
              // });
            }
            const _idsArr = [];
            data.file_info.forEach(file => {
              file._fileId = file.id;
              // file._type = vm.typeFilter(file.extension);
              const extension = getExtension(file.oldName);
              file.type = extension2Type(extension);
              file._name = file.oldName;
              vm.$set(file, "dialogVisible", false);
              _idsArr.push(file._fileId);
            });
            if (willReplace) {
              vm.uploadedIds = [];
              vm.uploadedFiles = [];
            }
            vm.uploadedIds = vm.uploadedIds.concat(_idsArr);
            vm.uploadedFiles = vm.uploadedFiles.concat(data.file_info);
            this.existedFiles = data.file_info;
          } else {
            vm.$message({
              type: "error",
              message: data.info
            });
          }
        });
    },
    getFilesNum() {
      const vm = this;
      return {
        limitNum: vm.limitNum,
        uploadedNum: vm.uploadedFiles.length,
        existedNum: vm.existedFiles.length,
        waitingNum: vm.waitingNum
      };
    },
    /**
     * @description 
     * @param delFromServer {Boolean} 
     */
    resetUpload(delFromServer = false) {
      if (delFromServer) {
        this.postDeleteRequest(this.uploadedIds);
      }
      this.uploader.fileList = [];
      this.uploader.files = [];
      this.uploadedFiles = [];
      this.existedFiles = [];
      this.addedNum = 0;
      this.waitingNum = 0;
    }
  },
  watch: {
    uploadedIds: function(val) {
      let vm = this;
      console.log("uploadedIds");
      this.$emit("listChange", val, vm.uploadedFiles);
    },
    fileIds(val) {
      this.loadFiles(this.fileIdsString);
      this.addedNum = Array.isArray(val) ? val.length : val.split(",").length;
    },
    docURL(value, oldVal) {
      if (typeof value === "undefined" || typeof oldVal === "undefined") {
        return false;
      }
      const uploadAPI = this.APIMap.get("upload");
      this.$refs.uploader.uploader.opts.target = uploadAPI;
      if (value.length !== 0 && oldVal.length === 0) {
        this.loadFiles(this.fileIdsString);
      }
    }
  }
};
</script>
<style lang="scss">
$background-blue: #409eff;
$background-green: #67c23a;
$light-blue: #f5f7fa;
@mixin clearfix {
  &::after {
    content: "";
    display: table;
    font-size: 0;
    height: 1px;
    clear: both;
  }
}
.upload {
  * {
    box-sizing: border-box;
  }
  .upload-drop {
    background: #fff;
    text-align: center;
    margin: 10px 0;
    i {
      font-size: 20px;
    }
  }
  .upload-btns {
    margin: 10px 0;
    .uploader-btn {
      color: #fff;
    }
    .add-file {
      background: $background-blue;
      border-color: $background-blue;
    }
    .start-upload {
      background: $background-green;
      border-color: $background-green;
    }
  }
  .file {
    @include clearfix;
    &:hover {
      background: $light-blue;
    }
    > div[class^="file"] {
      float: left;
      text-align: center;
    }
    .file-name {
      width: 45%;
      text-align: left !important;
      padding-left: 10px;
      > i {
        margin-right: 5px;
      }
    }
    .file-size,
    .file-status {
      width: 10%;
    }
    .file-finished {
      width: 30%;
    }
    .file-progress {
      width: 20%;
      .file-progress-wrap {
        position: relative;
        width: 100%;
        height: 20px;
        border-radius: 10px;
        background: #c5c5c5;
        overflow: hidden;
        .file-progress-current {
          transition: width 0.3s linear 0s;
          height: 100%;
          background: #409eff;
          &.success {
            background: #67c23a;
          }
          &.error {
            background: #f56c6c;
          }
        }
        .file-progress-number {
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          margin: auto;
          text-align: center;
          line-height: 20px;
          color: #fff;
        }
      }
    }
    .file-actions {
      width: 15%;
      a {
        font-size: 16px;
        color: #797979;
        margin-right: 5px;
        &:hover {
          color: darken($color: #797979, $amount: 30%);
        }
      }
    }
  }
}

.uploader-file,
.file {
  position: relative;
  border-bottom: 1px solid #cdcdcd;
  height: auto !important;
  line-height: normal !important;
  padding: 10px 0;
  overflow: hidden;
}
.hide {
  display: none;
}
.tips {
  font-size: 14px;
  color: #bbb;
}
.preview-image {
  width: 100%;
}
</style>



